import Table from '../../mo/models';
import TableCell from '../../mo/models/tableCell';

/**
 * 获取 TableCell 数据模型对应的 width（百分比或绝对宽度）
 */
export default function getTableCellWidth(cell: TableCell, table: Table) {
  if (!table || !Array.isArray(table?.data?.colsWidth)) {
    return null;
  }

  const row = table.getParent(cell.key);
  if (!row) return null;

  const colIndex = row.nodes.findIndex((c) => c.key === cell.key);
  if (colIndex === -1) return null;
  const colNum = cell.data?.colSpan || 1;
  const colSpan = (typeof colNum === 'number' ? colNum : parseInt(colNum, 10)) || 1;
  return table.data.colsWidth
    .slice(colIndex, colIndex + colSpan)
    .reduce((acc, v) => acc + v, 0);
}
